@import "../../inc/vars";
.wp-block-acrobits-incentive-panel {
	.incentive_panel {
		background-color: #FAF8FF;

		.incentive_wrapper {
			position: relative;
			z-index: 2;
		}
		.title_area {
			position: relative;
			z-index: 2;
			h3 {
				@include titleGrad();
				@include pop(400, 35, 1.24);
				letter-spacing: -0.7px;
				@media (min-width: $screen-sm){
					@include pop(400, 44, 1.12);
					letter-spacing: -0.88px;
				}
			}
		}
	}
}